.gpt3__poss {
  display: flex;
  flex-direction: row;
}
.gpt3__poss-image {
  flex: 1;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-right: 2rem;
}
.gpt3__poss-image img {
  width: 100%;
  height: 100%;
}
.gpt3__poss-content {
  flex: 1;
  flex-direction: column;
  display: flex;
  justify-content: flex-end;
  align-items: flex-start;
}
.gpt3__poss-content h4 {
  font-family: var(--font-family);
  font-size: 16px;
  font-weight: 500;
  line-height: 30px;
  color: #71e5ff;
}
.gpt3__poss-content h4:last-child {
  color: #ff8a71;
}
.gpt3__poss-content h1 {
  font-family: var(--font-family);
  font-size: 34px;
  font-weight: 800;
  line-height: 45px;
  margin: 1rem 0;
}
.gpt3__poss-content p {
  font-family: var(--font-family);
  font-size: 16px;
  font-weight: 400;
  line-height: 30px;
  color: var(--color-text);
  margin-bottom: 2rem;
}
@media screen and (max-width:950px) {
  .gpt3__poss {
    flex-direction: column;
  }
  .gpt3__poss-image {
    margin: 1rem 0;
  }
  .gpt3__poss-content{
    margin-top: 2rem;
  }
}
